body {
  margin: 0;
  padding: 0;
  font-family: 'Overpass', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  overflow: hidden;

  background-color: #000;
}

.ExampleViewer_exampleSign__mjiiM {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    display: grid;
}

.ExampleViewer_display__12Ce1 {
    grid-area: display;
    min-height: 384px;
    background-color: #009140;
}

.ExampleViewer_displayInner__1bK_d {
    border: 5px solid #fff;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.ExampleViewer_signContainer__3UHZM {
    height: 256px;
}

.ExampleViewer_sidebar__3Ayod {
    grid-area: sidebar;
    background-color: #007dc2;
    color: #fff;
    font-size: 18px;
}

.ExampleViewer_sidebarInner__Wux3H {
    border: 5px solid #fff;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    padding: 10px;
}

.ExampleViewer_sidebarElement__64uyR {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    padding: 10px 10px 30px;
}

.ExampleViewer_sidebarElement__64uyR > span {
    margin-bottom: 10px;
}

.ExampleViewer_signText__vOQB6 {
    font-weight: 600;
    font-size: 1.4rem;
    color: #fff;
}

@media only screen and (max-width: 600px) {
    .ExampleViewer_exampleSign__mjiiM {
        grid-template-columns: 1fr;
        grid-template-rows: 1fr fit-content(200px);
        grid-template-areas: "display" "sidebar";
    }

    .ExampleViewer_displayInner__1bK_d {
        border-radius: 10px 10px 0 0;
        border-right-width: 5px;
        border-bottom-width: 3px;
    }

    .ExampleViewer_sidebarInner__Wux3H {
        border-radius: 0 0 10px 10px;
        border-top-width: 2px;
    }
}

@media only screen and (min-width: 601px) {
    .ExampleViewer_exampleSign__mjiiM {
        grid-template-columns: 1fr 400px;
        grid-template-rows: 1fr;
        grid-template-areas: "display sidebar";
    }

    .ExampleViewer_displayInner__1bK_d {
        border-radius: 10px 0 0 10px;
        border-right-width: 3px;
    }

    .ExampleViewer_sidebarInner__Wux3H {
        border-radius: 0 10px 10px 0;
        border-left-width: 2px;
    }
}


